      checkAllFiltersOff: function() {
        for (var i in this.filters) {
          if (this.filters[i]) {
            return false;
          }
        }
        if (this.searchFilter != "") {
          return false;
        }
        return true;
      },
      resetFilters: function() {
        for (var i in this.filters) {
          this.filters[i] = false;
        }
        this.searchFilter = "";
        this.filteredTable = this.charadata;
      },
      filterAtktype: function() {
        var filteredArray = {};
        for (let i of this.charadata) {
          if (this.filters.normalFilter) {
            if (i.atktype == "normal") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.specialFilter) {
            if (i.atktype == "special") {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filterElement: function() {
        var filteredArray = {};
        for (let i of this.charadata) {
          if (this.filters.flowerFilter) {
              if (i.element == "flower") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.windFilter) {
              if (i.element == "wind") {
                filteredArray[i.ID]= i;
