          }
          if (this.filters.dreamFilter) {
              if (i.element == "dream") {
                filteredArray[i.ID]= i;
              }
          }
        }
        return filteredArray;
      },
      filterPosition: function() {
        var filteredArray = {};

        for (let i of this.charadata) {

          if (this.filters.frontFilter) {
            if (i.position == "front") {
              filteredArray[i.ID]= i;
